import { useSelector } from 'react-redux';
import { Link } from 'react-router-dom';

import styled from 'styled-components';
import { convertTimeFormat } from 'utils';

import { RootState } from 'redux/store';

import Button from 'components/Button';
import Chart from 'components/Chart';
import Exception from 'components/Exception';
import MarginBox from 'components/MarginBox';
import Title from 'components/Title';

const QuizResult = () => {
  const { correct, wrong, startTime, endTime } = useSelector((state: RootState) => state.quizSolve);

  if (correct.length === 0 && wrong.length === 0) {
    return (
      <Exception title={'   .'} path={'/quiz'} buttonText={' '} />
    );
  }

  return (
    <S.Container>
      <Title> </Title>
      <MarginBox bottom={2} />
      <S.ResultBox>
        <S.ResultContent>
          <h3> </h3>
          <S.Text>{convertTimeFormat(startTime, endTime)}</S.Text>
        </S.ResultContent>
        <S.ResultContent>
          <h3> </h3>
          <S.Text>{`${correct.length} `} </S.Text>
        </S.ResultContent>
        <S.ResultContent>
          <h3> </h3>
          <S.Text>{`${wrong.length} `}</S.Text>
        </S.ResultContent>
      </S.ResultBox>
      <MarginBox bottom={2} />
      <Chart labels={['', '']} labelsValue={[correct.length, wrong.length]} />
      <MarginBox bottom={1} />
      <S.ButtonBox>
        <Link to={'/quiz'}>
          <Button backColor={'GREEN'} hoverBackColor={'LIGHT_GREEN'} fontColor={'WHITE'}>
             
          </Button>
        </Link>
        <Link to={'/wrong-answer-note'}>
          <Button backColor={'GREEN'} hoverBackColor={'LIGHT_GREEN'} fontColor={'WHITE'}>
             
          </Button>
        </Link>
      </S.ButtonBox>
    </S.Container>
  );
};

const S = {
  Container: styled.div`
    width: 100%;
  `,

  ResultBox: styled.div`
    display: flex;
    justify-content: center;
    gap: 1rem;
  `,

  ResultContent: styled.div`
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 1rem;
  `,

  Text: styled.p`
    font-size: 1.25rem;
  `,

  ButtonBox: styled.div`
    display: flex;
    flex-direction: row;
    justify-content: center;
    gap: 1rem;
  `,
};

export default QuizResult;
